import React from "react"
import Header from "../components/header"
import Clock from "../components/clock"
import Textception from "../components/text_tricks"
import disco_quokka from "../../public/static/disco quokka.png"

export default function Random() {
    return (
        <div style={{ color: `teal` }}>
            <h1>Some fun visualizations using imgaug</h1>
            <img src={disco_quokka} width="300" height="300" alt="disco quokka" title="disco quokka"></img>
            <Clock />
            <Textception array={[[1,2,3],[4,5,6]]} letter="b"/>
        </div>
    )
}