import React from 'react';
//import ReactDOM from 'react-dom';


export default class Textception extends React.Component {
    constructor(props) {
        super(props);
        this.state = {array: props.array, letter: props.letter, date: new Date()};
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(), 1000
        );
    }
    componentWillUnomount() {
        clearInterval(this.timerID);
    }

    tick() {
        this.setState({
            date: new Date()
        });
    }

    render() {
        return (
            <div style={{fontFamily: "Courier New"}}>
                <table>
                    <tr>
                    {[1,2,3,4, 5, 6].map(function(object, i) {
                        return <td>{i}</td>;
                    })}
                    </tr>

                    <tr>
                        <td>hi there</td>
                        <td>hello again</td>
                        <td>there's more of me</td>
                    </tr>
                    <tr>
                        <td>hi there</td>
                        <td>hello again</td>
                        <td>there's more of me</td>
                    </tr>
                </table>

                <table>
                {this.state.array.map(function(arr, i) {
                    return <tr>{arr.map(function(subarr, j) {return <td>{i}:{j}</td>;})}</tr>
                })}
                </table>        

                <h2>It is {this.state.date.toLocaleTimeString()}.</h2>
            </div>
        )
    }
}